.card {
  position: relative;
  overflow: hidden;
  display: block;

  @media screen and (max-width: 768px) {
    width: 400px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  img {
    transition: transform 0.4s ease;
  }

  .card__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    color: $orange;
    background-color: rgba($dark, 0.8);
    transition: all 0.4s ease;
    font-family: "Reem Kufi", sans-serif;
    font-optical-sizing: auto;
    font-weight: 700;
    text-transform: uppercase;
    padding: 2rem;
    letter-spacing: rem(8);
    font-size: rem(18);
    text-align: center;

    &--small {
      font-size: rem(16);
      letter-spacing: 0.3em;
    }
  }

  &:hover {
    img {
      transform: scale(1.1);
    }

    .card__overlay {
      opacity: 1;
    }
  }
}
